main {
    width: 100vw;
    height: 100vh;
    @include flexCenter;
    background-color: $primary-color;
}

button {
    border: none;
}

.player-container {
    position: absolute;
    background-color: black;
    //position: relative;
    width: 15rem;
    //height: 30rem;
    border: solid white;
    @include btnShadow;
    border-radius: 2rem;
    @include flexColumn;
    h2 {
        font-size: clamp(1rem, 1.2vw, 4rem);
        margin: 0;
    }
    p {
        font-size: clamp(1rem, 1vw, 2rem);
        margin: 0;
    }
}

.songslist-container {
    @include flexColumn;
    width: 100%;
    height: 12rem;
    padding-bottom: 1rem;
    .songslist-header {
        width: 50%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: 0 auto clamp(.5rem, 1vw, 6rem);
        &__img {
            width: 4rem;
            height: 4rem;
            img {
                @include imageFit;
            }
        }
        p {
            margin-top: .5rem;
        }
    }
    ul {
        @include flexColumn;
        overflow: scroll;
        padding: 0;
    }
    &.mini {
        height: 0;
    }
}

.song-listed {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    width: 100%;
    &::before {
        content: '';
        display: block;
        background-color: white;
        width: .5rem;
        height: .5rem;
        margin: 0 .5rem;
        border-radius: 50%;
    }
    &__title {
        width: 85%;
        overflow: hidden;
    }
    p {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
}