@mixin position ($top: 0, $left: 0) {
    top: $top;
    left: $left;
}

@mixin btnShadow ($color: $third-color)  {
    box-shadow: .3vw .3vw 1vw $color;
    @media screen and (max-width:$breakpoint-mobile) {
        box-shadow: .3rem .3rem 1rem $color;
    }
}

@mixin btnHoverShadow ($color: $third-color)  {
    box-shadow: .5vw .5vw 1vw $color;
    @media screen and (max-width:$breakpoint-mobile) {
        box-shadow: .5rem .5rem 1rem $color;
    }
}

@mixin blury-shadow ($offsetX: 0px, $offsetY: 0px, $blur: 18px, $spread: 5px){
    box-shadow: $offsetX $offsetY $blur $spread black inset;
}

@mixin flexColumn {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin mobile-version {
    @media screen and (max-width:$breakpoint-mobile) {
        @content;
    }
}

@mixin tab-version {
    @media screen and (max-width:$breakpoint-ipad) {
        @content;
    }
}

@mixin iconBrands ($content: "\f09a", $font-size: clamp(1.4rem, 1.8vw, 4rem)) {
    content: $content;
    font-family: 'Font Awesome 6 brands';
    font-weight: 400;
    font-size: $font-size;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    margin-right: .5vw;
}

@mixin iconEmpty ($content: "\f005", $font-size: 1.4em) {
    content: $content;
    font-family: 'Font Awesome 6 empty';
    font-weight: 400;
    font-size: $font-size;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
}

@mixin icon ($content: "\f005", $font-size: clamp(1.4rem, 1.5vw, 4rem)) {
    content: $content;
    font-family: 'Font Awesome 6';
    font-weight: 900;
    font-size: $font-size;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
}

@mixin instagramColor {
    background: linear-gradient(49.2deg,#f9ed32 10%,#ee2a7b 55%,#002aff);
}

@mixin activeColors {
    background-color: $secondary-color-dark;
    color: white;
}

@mixin imageFit {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@mixin titleShadow {
    text-shadow: 2px 2px 1px $primary-color;
}

@mixin textShadow ($color: $third-color) {
    text-shadow: .2vw .2vw .1vw $color;
    @media screen and (max-width:$breakpoint-mobile) {
        text-shadow: .2rem .2rem .1rem $color;
    }
}

@mixin cursorK {
    cursor: url(./img/logo_K_micro.webp), auto;
}