.controls-container {
    width: 90%;
    border: solid;
    display: flex;
    justify-content: space-around;
    border-radius: 1rem;
    margin: .5rem auto;
    button {
        width: 2rem;
        height: 2rem;
        background-color: $third-color;
        color: white;
        text-shadow: 2px 2px 1px black;
        &:nth-child(1) {
            border-radius: 1rem 0 0 1rem;
            &::before {
                @include icon ('\f049',1.2rem)
            }
        }
        &:nth-child(2) {
            &::before {
                @include icon ('\f04a',1.2rem)
            }
        }
        &:nth-child(4) {
            &::before {
                @include icon ('\f04e',1.2rem)
            }
        }
        &:nth-child(5) {
            &::before {
                @include icon ('\f050',1.2rem)
            }
        }
        &:nth-child(6) {
            border-radius: 0 1rem 1rem 0;
            &::before {
                @include icon ('\f04d',1.2rem)
            }
        }
    }
}

.song-play::before {
    @include icon ('\f04b',1.2rem)
}
.song-pause::before {
    @include icon ('\f04c',1.2rem)
}