.player-header {
    @include flexColumn;
    width: 90%;
}

.header-top {
    align-self: center;
    width: 90%;
    height: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logo-Kobutsune {
        width: 2rem;
        height: 2rem;
        img {
            @include imageFit;
        }
    }
    .arrow-mini {
        background-color: black;
    }
    .opened::before {
        @include icon('\f422',1.4rem);
    }
    .closed::before {
        @include icon('\f424',1.4rem);
    }
}



.playing-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    &__scrolling {
        white-space: nowrap;
        background-color: #fff;
        overflow: hidden;
        display: flex;
        padding: .3rem 0 .3rem .3rem;
        display: flex;
        border-radius: .5rem;
        h2 {
            color: black;
        }
        &.scroll h2 {
            animation: scrolling-title 4s 200ms infinite linear;
        }
    }
}

.header-anim {
    width: 100%;
    height: 4rem;
    margin: .5rem 0;
    display: flex;
    overflow: hidden;
    border-bottom: solid;
    &.animated div {
        animation: moving 600ms infinite alternate;
    }
    div {
        background-color: $third-color;
        width: 12%;
        height: 100%;
        margin: 0 .2vw;
        transform: translateY(-4rem);
        &:nth-child(1) {
            animation-delay: 200ms;
        }
        &:nth-child(2) {
            animation-delay: 100ms;
        }
        &:nth-child(3) {
            animation-delay: 400ms;
        }
        &:nth-child(4) {
            animation-delay: 50ms;
        }
        &:nth-child(5) {
            animation-delay: 300ms;
        }
        &:nth-child(6) {
            animation-delay: 150ms;
        }
        &:nth-child(7) {
            animation-delay: 500ms;
        }
    }

}